.studioiwill-About-root-container {
	margin: 0 140px;
	width: calc(100vw - 280px);
	max-width: 100%;
	overflow: hidden;

	.preview-area {
		width: 100%;
		padding: 20px 0;
		border-bottom: 0.5px solid #707070;
		border-top: 0.5px solid #707070;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;

		.preview {
			width: 100%;
			max-width: 1350px;
			height: 900px;
			object-fit: contain;
		}

		.preview-slider-area {
			margin: 30px 0;
			// width: 100%;
			max-width: 1350px;
			display: flex;
			justify-content: center;
			align-items: center;

			.preview-slider {
				position: relative;
				// width: 100%;
				max-width: 1350px;
				// padding: 0 32px;

				.preview-img {
					height: 146px;
					cursor: pointer;
				}

				.about-slider-btn {
					display: flex;
					position: absolute;
					top: 0;
					align-items: center;
					cursor: pointer;
					height: calc(100% - 3px);

					.slider-btn-img {
						width: 10px;
						height: 30px;
						padding: 0 10px;
					}

					&.prev {
						left: 0;
						// background: linear-gradient(to right, rgba(0, 0, 0, 0.3), transparent);
					}

					&.next {
						right: 0;
						// background: linear-gradient(to left, rgba(0, 0, 0, 0.3), transparent);
					}
				}
			}
		}

		@media (max-width: 1700px) {
			.preview {
			  height: 800px;
			  max-width: 1200px;
			}

			.preview-slider-area {
				max-width: 1200px;

				.preview-slider {
					max-width: 1200px;
				}
			}
		}

		@media (max-width: 1500px) {
			.preview {
			  height: 600px;
			  max-width: 900px;
			}

			.preview-slider-area {
				max-width: 900px;

				.preview-slider {
					max-width: 900px;
				}
			}
		}
		
		@media (max-width: 1200px) {
			.preview {
			  height: 400px;
			  max-width: 600px;
			}

			.preview-slider-area {
				max-width: 600px;

				.preview-slider {
					max-width: 600px;
				}
			}
		}

		@media (max-width: 900px) {
			.preview {
			  height: 250px;
			  max-width: 375px;
			}
			.preview-slider-area {
				max-width: 375px;

				.preview-slider {
					max-width: 375px;
				}
			}
		}
	}

	.about-area {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		margin: 60px 0;

		.about-logo-area {
			width: 180px;
			height: 30px;
		}

		.about-text {
			font-family: NanumSquareNeo;
			font-size: 26px;
			color: #252525;
			margin: 33px 0 20px 0;
		}
		
		.about-text2 {
			font-family: NanumSquareNeo;
			font-size: 18px;
			color: #252525;
			margin-top: 20px;
		}
	}
	
}

.slick-track {
	display: flex !important;
	gap: 16px;
}